.Banner{
    color: white;
    object-fit: contain;
    height: 448px;
}
.Banner-content{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}
.Banner-title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.Banner_desc{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
}
.Banner_Botton{
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color: rgb(51, 51, 51,0.5);
    padding-bottom: 0.5rem;
}
.Banner_Botton:hover{
    color: black;
    background-color: #fff;
    transition: all 0.2s;
}
.Banner-faded{
    height: 7.4rem;
    background-image: linear-gradient(
        180deg,
        transparent
        rgba(37,37,37,0.61)
        #111
    );
    
}